"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { HEADER_NAV, type HeaderMode } from "@/config/headerNav";

/**
 * Reference HTML 기반 헤더 구조:
 * - PC: 좌 로고 | 중앙 네비게이션 | 우 유틸리티 (검색, 햄버거)
 * - 메뉴 hover 시 .header.open 클래스 추가 → 배경색 #054DA6, 높이 45rem
 * - 스크롤 시 .header.fixed 클래스 추가 → box-shadow
 */
export default function Header() {
  const [scrolled, setScrolled] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [activeNavIndex, setActiveNavIndex] = useState<number | null>(null);
  const [headerOpen, setHeaderOpen] = useState(false);

  // 스크롤 감지
  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 30);
    window.addEventListener("scroll", onScroll, { passive: true });
    onScroll();
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  // 메뉴 오버레이 열림/닫힘 처리
  useEffect(() => {
    if (menuOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "";
    }
    return () => { document.body.style.overflow = ""; };
  }, [menuOpen]);

  // 네비게이션 hover 처리
  const handleNavMouseEnter = (index: number) => {
    setActiveNavIndex(index);
    setHeaderOpen(true);
  };

  const handleNavMouseLeave = () => {
    setActiveNavIndex(null);
    setHeaderOpen(false);
  };

  // 현재 모드 (aesthetic 모드 사용)
  const currentMode: HeaderMode = "aesthetic";
  const navItems = HEADER_NAV[currentMode];

  return (
    <>
      <header className={`header ${scrolled ? "fixed" : ""} ${headerOpen ? "open" : ""}`}>
        <div className="hd_in">
          {/* 로고 */}
          <h1 className="logo">
            <Link href="/">
              <Image
                src="/logo-hommage.png"
                alt="Hommage Clinic"
                width={220}
                height={100}
                className="bk"
                priority
              />
              <Image
                src="/logo-only-w.png"
                alt="Hommage Clinic"
                width={220}
                height={100}
                className="wh"
                priority
              />
            </Link>
          </h1>

          {/* 네비게이션 (PC) */}
          <nav className="nav">
            <ul className="depth1">
              {navItems.map((item, index) => (
                <li
                  key={index}
                  onMouseEnter={() => item.subs && handleNavMouseEnter(index)}
                  onMouseLeave={handleNavMouseLeave}
                  className={activeNavIndex === index ? "active" : ""}
                >
                  <Link href={item.link}>
                    <span>{item.title}</span>
                  </Link>

                  {/* 서브메뉴 */}
                  {item.subs && (
                    <div className={`depth2 ${activeNavIndex === index ? "on" : ""}`}>
                      <div className="cont">
                        <div className="txt_box">
                          <p className="tit">{item.title}</p>
                          <ul>
                            {item.subs.map((sub, subIndex) => (
                              <li key={subIndex} className={subIndex === 0 ? "active" : ""}>
                                <Link href={sub.link}>{sub.name}</Link>
                              </li>
                            ))}
                          </ul>
                        </div>
                        {/* 일러스트 영역 (선택사항) */}
                        <div className="illu"></div>
                      </div>
                    </div>
                  )}
                </li>
              ))}
            </ul>
          </nav>

          {/* 유틸리티 영역 */}
          <div className="util_wrap">
            {/* 검색 버튼 (PC) */}
            <div className="util_box">
              <button type="button" className="ico sch hd_pc" aria-label="검색">
                <svg className="hdUtil" xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 512 512">
                  <path d="M368 208A160 160 0 1 0 48 208a160 160 0 1 0 320 0zM337.1 371.1C301.7 399.2 256.8 416 208 416C93.1 416 0 322.9 0 208S93.1 0 208 0S416 93.1 416 208c0 48.8-16.8 93.7-44.9 129.1L505 471c9.4 9.4 9.4 24.6 0 33.9s-24.6 9.4-33.9 0L337.1 371.1z" />
                </svg>
              </button>

              {/* 햄버거 버튼 */}
              <button
                type="button"
                className="ico ham_btn"
                onClick={() => setMenuOpen(true)}
                aria-label="메뉴 열기"
              >
                <svg className="hdUtil" xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 448 512">
                  <path d="M0 88C0 74.7 10.7 64 24 64H424c13.3 0 24 10.7 24 24s-10.7 24-24 24H24C10.7 112 0 101.3 0 88zM0 248c0-13.3 10.7-24 24-24H424c13.3 0 24 10.7 24 24s-10.7 24-24 24H24c-13.3 0-24-10.7-24-24zM448 408c0 13.3-10.7 24-24 24H24c-13.3 0-24-10.7-24-24s10.7-24 24-24H424c13.3 0 24 10.7 24 24z" />
                </svg>
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* 모바일 메뉴 오버레이 */}
      <div className={`sitemap notranslate ${menuOpen ? "on" : ""}`}>
        <div className="cont">
          <div className="mo_menu">
            <div className="menu">
              <button
                type="button"
                className="close_btn"
                onClick={() => setMenuOpen(false)}
                aria-label="메뉴 닫기"
              >
                <svg xmlns="http://www.w3.org/2000/svg" height="1em" viewBox="0 0 448 512">
                  <path d="M64 32C28.7 32 0 60.7 0 96V416c0 35.3 28.7 64 64 64H384c35.3 0 64-28.7 64-64V96c0-35.3-28.7-64-64-64H64zm79 143c9.4-9.4 24.6-9.4 33.9 0l47 47 47-47c9.4-9.4 24.6-9.4 33.9 0s9.4 24.6 0 33.9l-47 47 47 47c9.4 9.4 9.4 24.6 0 33.9s-24.6 9.4-33.9 0l-47-47-47 47c-9.4 9.4-24.6 9.4-33.9 0s-9.4-24.6 0-33.9l47-47-47-47c-9.4-9.4-9.4-24.6 0-33.9z" />
                </svg>
              </button>
              <ul className="list">
                {navItems.map((item, index) => (
                  <li key={index} className="depth1">
                    <Link href={item.link} onClick={() => setMenuOpen(false)}>
                      {item.title}
                    </Link>
                    {item.subs && (
                      <ul className="depth2">
                        {item.subs.map((sub, subIndex) => (
                          <li key={subIndex} className={subIndex === 0 ? "active" : ""}>
                            <Link href={sub.link} onClick={() => setMenuOpen(false)}>
                              {sub.name}
                            </Link>
                          </li>
                        ))}
                      </ul>
                    )}
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
